import Layout from "../../components/Layout";
import Spinner from "../../components/Spinner";
import displayError from "../../utils/displayError";
import styles from "./styles.module.css"
import { useTitle } from "../../utils/useTtile";
import { useStats, useStatsCacheClear } from "../../api/other";

export default function StatsPage()
{
	useTitle("");
	const stats = useStats();
	const clearCache = useStatsCacheClear();

	return (
		<Layout centered homeBtn gap="1rem" height100 className={styles.root} forStaff>
			{stats.isLoading && <Spinner />}
			{displayError(stats)}
			<h2></h2>
			<button disabled={stats.isFetching} className={styles.btn} onClick={() => { clearCache(); stats.refetch(); }}></button>
			<div className={styles.groupStats}>
				<h3></h3>
				<div>
					<h4> 1</h4>
					<ul>
						<li>: {stats.data?.group1_members ?? "N/A"}</li>
						<li>  : {stats.data?.group1_completed ?? "N/A"}</li>
					</ul>
				</div>
				<div>
					<h4> 2</h4>
					<ul>
						<li>: {stats.data?.group2_members ?? "N/A"}</li>
						<li>  : {stats.data?.group2_completed ?? "N/A"}</li>
					</ul>
				</div>
			</div>
		</Layout>
	);
}
